@import '~antd/dist/antd.less';
@import './theme/index.less';

.ant-table-thead>tr>th,
.ant-table-tbody>tr>td {
    text-align: center;
}

input:focus-visible {
    outline: 0;
}

.ant-checkbox-inner {
    border-radius: 0;
}

.ant-modal-confirm-body .ant-modal-confirm-title,
.ant-modal-confirm-body .ant-modal-confirm-content {
    text-align: center;
}

.ant-row {
    justify-content: center;
}

::-webkit-scrollbar {
    display         : none;
    width           : 3px;
    height          : 3px;
    background-color: #fff;
}

.ant-image {
    width: 100px;
}

.delIcon {
    position : absolute;
    top      : 0px;
    right    : 0px;
    font-size: 18px;
    cursor   : pointer;
}
@primary-color: #1DA57A;@border-radius-base: 12px;